    padding-left: 39px;
    margin-bottom: 18px;
  }
}
@media (min-width: 1440px) {
  .checkbox-group {
    padding-left: 7px;
  }
}

.checkbox-group__title {
  padding: 0;
  margin-bottom: 22px;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
}

.checkbox-group__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.checkbox-group__item {
  margin-bottom: 20px;
}

.checkbox-group__control {
  position: relative;
  display: block;
  padding-left: 52px;
}

.checkbox-group__mark {
  position: absolute;
  top: 0;
  left: 0;
  width: 42px;
  height: 24px;
  background-color: #bdbdbd;
  cursor: pointer;
}
.checkbox-group__mark::after {
  position: absolute;
  top: 2px;
  left: 2px;
  width: 20px;
  height: 20px;
  background-color: #ffffff;
  border-radius: 50%;
  content: "";
}
.checkbox-group__mark:hover, .checkbox-group__mark:focus {
  outline: none;
  border: none;
  position: absolute;
  left: 0;
  width: 42px;
  height: 24px;
  background-image: url("../img/svg/sprites/chek-hover-pin.svg");
  background-size: 42px 24px;
  background-repeat: no-repeat;
  content: "";
}
.checkbox-group__mark--disabled {
  background-color: #e2e2e2;
}

.checkbox-group__input + .checkbox-group__mark {
  border-radius: 12px;
}

.checkbox-group__input:checked + .checkbox-group__mark::before {
  position: absolute;
  top: 2px;
  right: 2px;
  width: 20px;
  height: 20px;
  background-color: #ffffff;
  border-radius: 50%;
  content: "";
  z-index: 1;
}
.checkbox-group__input:checked + .checkbox-group__mark::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 42px;
  height: 24px;
  background-color: #7859cf;
  border-radius: 12px;
}

.checkbox-group__label {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
}
.checkbox-group__label--disabled {
  color: #bdbdbd;
}

